"use client"

import { motion } from "framer-motion"
import { Smartphone, RefreshCw, DollarSign } from "lucide-react"
import Link from "next/link"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"

const airtimeServices = [
  {
    title: "VTU",
    description: "Virtual Top-Up for instant airtime recharge on all networks",
    icon: Smartphone,
    href: "/services/airtime/vtu",
    color: "bg-[#3457D5]",
  },
  {
    title: "Share 'n Sell",
    description: "Share and sell airtime to other users and earn commissions",
    icon: RefreshCw,
    href: "/services/airtime/share-n-sell",
    color: "bg-[#3457D5]",
  },
  {
    title: "Airtime 2 Cash",
    description: "Convert your excess airtime to cash instantly",
    icon: DollarSign,
    href: "/services/airtime/airtime-2-cash",
    color: "bg-[#3457D5]",
  },
]

export default function AirtimeServicesPage() {
  return (
    <div className="space-y-8">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="mb-8"
      >
        <h1 className="text-4xl font-bold text-[#3457D5] mb-2">Airtime Services</h1>
        <p className="text-muted-foreground">
          Purchase airtime, share with others, or convert to cash - all in one place
        </p>
      </motion.div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {airtimeServices.map((service, index) => {
          const Icon = service.icon
          return (
            <motion.div
              key={service.href}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
            >
              <Link href={service.href}>
                <Card className="h-full hover:shadow-lg transition-shadow cursor-pointer border-[#CCCCFF]/20">
                  <CardHeader>
                    <div className={`${service.color} w-12 h-12 rounded-lg flex items-center justify-center mb-4`}>
                      <Icon className="h-6 w-6 text-white" />
                    </div>
                    <CardTitle className="text-[#3457D5]">{service.title}</CardTitle>
                    <CardDescription>{service.description}</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <span className="text-sm text-[#3457D5] font-medium hover:underline">Get Started â†’</span>
                  </CardContent>
                </Card>
              </Link>
            </motion.div>
          )
        })}
      </div>
    </div>
  )
}
